import Vue from 'vue'
import Router from 'vue-router'



// 导航栏
const navbox = resolve => require(['@/components/utils/nav.vue'], resolve)
// import navbox from '@/components/utils/nav.vue'
// 测试页面
const test = resolve => require(['@/components/test'], resolve)
// 首页关于我们
const about = resolve => require(['@/components/about/about'], resolve)
// 证照
const licence = resolve => require(['@/components/about/licence'], resolve)
// 合作品牌
const cooperation = resolve => require(['@/components/about/cooperation'], resolve)
Vue.use(Router)

export default new Router({
	// mode: 'history',
	// base: process.env.BASE_URL,
	routes: [
		{
			path: '/',
			name: 'index',
			redirect: "/about",
		},
		{
			// 未写页面全部跳转测试页面
			path: '/test',
			component: test
		},
		// 关于的子页面
		{
			path: '/about',
			name: "about",
			component: navbox,
			// redirect: '/about',
			children: [
				{	
					// 关于
					path: '/about',
					component: about
				},
				{
					// 证照
					path: 'licence',
					component: licence
				},
				{
					// 合作品牌
					path: 'cooperation',
					component: cooperation
				}
			]
		},
		// {
		// 	path: '/about/licence',
		// 	component: licence
		// },
		// {
		// 	path: '/about/cooperation',
		// 	component: cooperation
		// }
	]
})



